<template>
  <div>
    <p>名称：
      <span class="total">{{formData.name}}</span>
    </p>
    <p>销售总收入：
      <span class="total">{{income}}</span>
    </p>
    <p>总补贴：
      <span class="total">+{{allowance}}</span>
    </p>
    <p>总成本：
      <span class="total">-{{cost}}</span>
    </p>
    <hr />
    <p>总盈亏：
      <span class="total" :class="income+allowance-cost>=0?'green':'red'">{{income+allowance-cost}}</span>
    </p>
  </div>
</template>

<script>
  export default {
    props: {
      formData: Object,
      step: Number
    },
    computed: {
      cost() {
        return (
          this.formData.area * this.formData.costPerArea +
          this.formData.personNumber * this.formData.costPerPerson +
          this.formData.otherCost
        );
      },
      income() {
        if (this.formData.category) {
          return Math.round(
            this.formData.category.reduce(
              (sum, item) =>
                sum +
                this.formData.sales *
                  item.saleRate *
                  item.grossProfitRate /
                  10000 +
                this.formData.sales *
                  item.saleRate *
                  item.stableReturnRate /
                  10000 +
                this.formData.sales *
                  item.saleRate *
                  item.returnValuePerMonth *
                  this.formData.returnRatePerMonth /
                  1000000,
              this.formData.returnValuePerQuarter *
                this.formData.returnRatePerQuarter /
                300
            )
          );
        } else {
          return 0;
        }
      },
      allowance() {
        return Math.round(
          this.formData.allowancePerQuarter / 3 + this.formData.allowanceForStart
        );
      }
    }
  };
</script>

<style scoped>
  .green {
    color: green;
  }
  .red {
    color: red;
  }
  .total {
    position: absolute;
    right: 10%;
    font-size: 15px;
    font-weight: bold;
  }
</style>



